	width: 100%;
	font-size: 0.34rem;
	padding: 0.4rem 0.3rem;
	border-bottom: 0.02rem solid #CCCCCC;
}
.star_item{
	width: 0.55rem;
	height: 0.5rem;
	margin-right: 0.5rem;
	float: left;
	background: url(../../../static/evaluate_null.png) no-repeat;
	background-size:100% 100% ;
}
.submit{
	width: 6.9rem;
	height: 0.8rem;
	border-radius:0.8rem ;
	background: #fa8a12;
	margin: 2.7rem auto;
	color: #FFFFFF;
	text-align: center;
	line-height: 0.8rem;
	font-size: 0.36rem;
}

</style>


